module.exports = "/*Loading*/\n.loading {\n  z-index: 201;\n  background: url('parchment-background.jpg');\n  width: 100vw;\n  height: 100vh;\n  display: flex;\n  flex-direction: column;\n  align-items: center;\n  justify-content: center;\n  position: fixed;\n  top: 0;\n  left: 0;\n}\n.background-fade {\n  -webkit-animation: fade 300ms 0.3s ease-out 1 normal forwards;\n          animation: fade 300ms 0.3s ease-out 1 normal forwards;\n}\n.logo-fade {\n  -webkit-animation: zoomout 200ms 0.2s cubic-bezier(0.6, -0.55, 0.735, 0.045) 1 normal forwards;\n          animation: zoomout 200ms 0.2s cubic-bezier(0.6, -0.55, 0.735, 0.045) 1 normal forwards;\n}\n.loading .hs-logo {\n  -webkit-animation: spin 1.5s 200ms ease-out infinite;\n          animation: spin 1.5s 200ms ease-out infinite;\n}\n/*Card List Child element with footer*/\n.card-container {\n  margin: 0;\n  width: 100vw;\n  min-height: 100vh;\n  background: url('parchment-background.jpg');\n  display: flex;\n  flex-direction: column;\n  align-items: center;\n  justify-content: flex-start;\n}\napp-card-view {\n  width: 100%;\n  flex-grow: 1;\n  transition: width 400ms ease-in-out;\n}\n/*Top Menu*/\n.main-header {\n  width: 100%;\n  position: fixed;\n  top: 0;\n  left: 0;\n  background: url('wood_middle_repeat.png');\n  background-position: bottom;\n  background-repeat: repeat-x;\n  padding: 0.2rem 1rem;\n  z-index: 200;\n  display: flex;\n  flex-direction: row;\n  align-items: center;\n  justify-content: space-between;\n  color: #EBD6AB;\n}\n.button-logo {\n  display: flex;\n  justify-content: center;\n  align-items: center;\n}\n.menu-logo h3 {\n  margin: 0;\n}\n.github-details {\n  margin-right: 15px;\n}\n.github-details:hover {\n  color: white;\n  cursor: pointer;\n}\n.github-details a {\n  color: inherit;\n}\n.github-details i {\n  font-size: 1.5rem;\n}\n.github-details p {\n  margin: 0 10px;\n  display: inline-block;\n}\n/*Slide Menus*/\n.side-menu-filter {\n  display: flex;\n  flex-direction: column;\n  align-items: center;\n  justify-content: center;\n  background: black;\n  color: #fff;\n  position: fixed;\n  left: auto;\n  top: 0;\n  left: 0;\n  bottom: 0;\n  width: 100vw;\n  z-index: 101;\n}\n.side-menu-detail {\n  display: flex;\n  flex-direction: column;\n  align-items: center;\n  justify-content: center;\n  padding-top: 60px;\n  background: black;\n  color: #fff;\n  position: fixed;\n  left: auto;\n  top: 0;\n  left: 0;\n  bottom: 0;\n  width: 100vw;\n  z-index: 102;\n}\n/*Burger Styles*/\n.bt-menu {\n  position: relative;\n  display: block;\n  width: 55px;\n  height: 55px;\n}\n.bt-menu:hover {\n  cursor: pointer;\n}\n.bt-menu:after, .bt-menu:before {\n  content: \"\";\n}\n.bt-menu span, .bt-menu::after, .bt-menu::before {\n  -webkit-backface-visibility: hidden;\n          backface-visibility: hidden;\n  position: absolute;\n  left: 13px;\n  right: 13px;\n  border-top: 4px solid #EBD6AB;\n  transition: .2s;\n  border-radius: 7px;\n}\n.bt-menu::before {\n  top: 18px;\n}\n.bt-menu span {\n  top: 26px;\n}\n.bt-menu::after {\n  top: 34px;\n}\n.bt-menu.active::before, .bt-menu.active::after {\n  top: 25px;\n  -webkit-backface-visibility: hidden;\n          backface-visibility: hidden;\n  transition: .3s;\n}\n.bt-menu.active::before {\n  -webkit-transform: rotate(45deg);\n          transform: rotate(45deg);\n}\n.bt-menu.active span {\n  opacity: 0;\n}\n.bt-menu.active::after {\n  -webkit-transform: rotate(-45deg);\n          transform: rotate(-45deg);\n}\n.disabled {\n  cursor: not-allowed;\n}\n/*footer styles*/\n.footer {\n  background: url('wood_middle_repeat.png');\n  background-position: center;\n  background-repeat: repeat-x;\n  width: 100%;\n  justify-self: flex-end;\n  color: #EBD6AB;\n  display: flex;\n  justify-content: flex-start;\n}\n.footer p {\n  margin: 0;\n  padding: 10px 25px;\n  width: 100%;\n  transition: width 400ms ease-in-out;\n  font-weight: normal;\n}\n.footer a {\n  color: inherit;\n  text-decoration: none;\n  padding: 0;\n}\n.footer a:hover {\n  color: white;\n}\n/deep/ .page-link {\n  color: black !important;\n}\n/deep/ .page-item.active .page-link {\n  background-color: black !important;\n  border-color: black !important;\n  color: white !important;\n}\n/* Larger desktop */\n@media (min-width: 1200px) {\n  .side-menu-filter,\n  .side-menu-detail {\n    width: 35vw;\n    background: url('hs_background.jpg') no-repeat center center fixed;\n    background-size: cover;\n  }\n  .card-container-open {\n    align-items: flex-end;\n  }\n  .card-container-open > app-card-view {\n    width: 65vw;\n    padding: 0 2%;\n  }\n  .footer.footer-open {\n    justify-content: flex-end;\n  }\n  .footer > p.footer-open {\n    width: 65%;\n  }\n}\n/* Large desktop */\n@media (min-width: 980px) and (max-width: 1199px) {\n  .side-menu-filter,\n  .side-menu-detail {\n    width: 50vw;\n    background: url('hs_background.jpg') no-repeat center center;\n    background-size: cover;\n  }\n  .card-container-open {\n    align-items: flex-end;\n  }\n  .card-container-open > app-card-view {\n    width: 50vw;\n    padding: 0 2%;\n  }\n  .footer.footer-open {\n    justify-content: flex-end;\n  }\n  .footer > p.footer-open {\n    width: 50%;\n  }\n}\n/* Portrait tablet to landscape and desktop */\n@media (min-width: 768px) and (max-width: 979px) {\n  .side-menu-filter,\n  .side-menu-detail {\n    width: 50vw;\n    background: url('hs_background.jpg') no-repeat center center fixed;\n    background-size: cover;\n  }\n  .card-container-open {\n    align-items: flex-end;\n  }\n  .card-container-open > app-card-view {\n    width: 50vw;\n    padding: 0 2%;\n  }\n  .footer.footer-open {\n    justify-content: flex-end;\n  }\n  .footer > p.footer-open {\n    width: 50%;\n  }\n}\n/* Landscape phone to portrait tablet */\n@media (max-width: 767px) {\n  .main-header {\n    padding: 0;\n  }\n}\n/* Landscape phones and down */\n@media (max-width: 480px) {\n  .main-header {\n    padding: 0;\n  }\n  .github-details p {\n    display: none;\n  }\n}\n/*spin animation*/\n@-webkit-keyframes spin {\n  5% {\n    -webkit-transform:rotate(0);\n    transform:rotate(0);\n  }\n  20% {\n    -webkit-transform:rotate(-65deg);\n    transform:rotate(-65deg);\n  }\n  100% {\n    -webkit-transform: rotate(1080deg);\n    transform:rotate(1080deg);\n\n  }\n}\n@keyframes spin {\n  5% {\n    -webkit-transform:rotate(0);\n    transform:rotate(0);\n  }\n  20% {\n    -webkit-transform:rotate(-65deg);\n    transform:rotate(-65deg);\n  }\n  100% {\n    -webkit-transform: rotate(1080deg);\n    transform:rotate(1080deg);\n\n  }\n}\n/*zoom out animation*/\n@-webkit-keyframes zoomout {\n  0% {\n    -webkit-transform: scale(1);\n            transform: scale(1);\n    opacity: 1;\n  }\n  100% {\n    -webkit-transform: scale(0.8);\n            transform: scale(0.8);\n    display: none;\n    visibility: hidden;\n    opacity: 0;\n  }\n}\n@keyframes zoomout {\n  0% {\n    -webkit-transform: scale(1);\n            transform: scale(1);\n    opacity: 1;\n  }\n  100% {\n    -webkit-transform: scale(0.8);\n            transform: scale(0.8);\n    display: none;\n    visibility: hidden;\n    opacity: 0;\n  }\n}\n/*fade animation*/\n@-webkit-keyframes fade {\n  0% {\n    opacity: 1;\n  }\n  100% {\n    display: none;\n    visibility: hidden;\n    opacity: 0;\n  }\n}\n@keyframes fade {\n  0% {\n    opacity: 1;\n  }\n  100% {\n    display: none;\n    visibility: hidden;\n    opacity: 0;\n  }\n}\n\n/*# sourceMappingURL=data:application/json;base64, */"